import React from "react";
import { Select, Form } from "antd";

const { Option } = Select;
const CSelectOptionActiva = ({
  label,
  name,
  placeholder,
  onChange,
  size,
  isDisabled,
  require,
  validate,
  value,
  ...rest
}) => {
  return (
    <>
      <Form.Item
        label={
          <span>
            {label}
            {require ? (
              <span style={{ marginLeft: "5px", color: "red" }}>*</span>
            ) : null}
          </span>
        }
        name={name}
        {...rest}
        validateStatus={validate ? "error" : null}
        help={validate}
      >
        <Select
          showSearch
          disabled={isDisabled}
          {...rest}
          style={{ minHeight: "100%" }}
          size={size}
          value={value}
          placeholder={placeholder}
          optionFilterProp="children"
          onChange={onChange}
          filterOption={(input, option) =>
            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >=
            0
          }
        >
          <Option key="YES" value="Y">
            YES
          </Option>
          <Option key="NO" value="N">
            NO
          </Option>
        </Select>
      </Form.Item>
    </>
  );
};

export default CSelectOptionActiva;
